<template>
  <!-- <img alt="Vue logo" src="./assets/logo.png">
  <HelloWorld msg="Welcome to Your Vue.js App"/> -->
  <h1>jin's reaction</h1>
  <button type="button" class="gamebutton" @click="startgame" :disabled="isPlaying">play</button>
  <Block v-if="isPlaying" :delay="delay" @end="endgame"/>
  <Result v-if="showR" :childScore="score" @click="hideResult"/>
  
</template>

<script>
//import HelloWorld from './components/HelloWorld.vue'
import Block from './components/Block.vue'
import Result from './components/Result.vue'
export default {
  name: 'App',
  components: {
    //HelloWorld
    Block, Result
  },
  data(){
    return{
      isPlaying : false,
      delay : null,
      score : null,
      showR : false
    }
  },
  methods:{
    startgame(){
      this.delay = 2000 + Math.random() * 5000;
      this.isPlaying=true;
      this.showR = false;
    },
    endgame(values){
      this.score = values;
      this.isPlaying = false;
      this.showR = true;
    },
    hideResult(){
      this.showR = false;
    }
  }
}
</script>

