<template>
    <div class="result" >{{ rank }} <br>{{ childScore }}ms</div>
    
</template>

<script>
export default {
    props:['childScore'],
    data(){
        return{
            rank:null
        }
    },
    mounted(){
        if(this.childScore<250)this.rank="A"
        else if(this.childScore<400)this.rank="B"
        else this.rank="C"
    }
}
</script>
methods
<style>
.result{
        width: 400px;
        border-radius: 20px;
        background-color: #ccd82b;
        color: rgb(32, 27, 27);
        text-align: center;
        padding:100px 0;
        margin:40px auto;
    }
</style>